import React from "react";
import { Col, Row, Image } from "react-bootstrap";
//import Pics from "./Carousel";
//import background from "../../img/background/back400.png";
import LogoHome from "../Partials/LogoHome";
import HomePic from "../../img/icons/icons/home/imagen home_01.png";
import Icon from "../../img/icons/icons/home/illustration home.png";
import Service01 from "../../img/icons/icons/home/imagen home_02.png";
import Service02 from "../../img/icons/icons/home/imagen home_03.png";
import Service03 from "../../img/icons/icons/home/imagen home_04.png";
import Title from "../Partials/Title";

const Picture = () => {
    return (
        <Row style={{ paddingLeft: '0', paddingRight: '0' }}>
            <Col lg={12} md={12} sm={12} xs={12} style={{ paddingLeft: '0', paddingRight: '0' }}>
                <Image src={HomePic} style={{ width: '100%' }} />
            </Col>
        </Row>
    )
}

const SecondSection = () => {
    return (
        <Row>
            <Col lg={5} md={5} sm={1} xs={1} className = 'homeFirst' >
                <Image src={Icon} />
            </Col>
            <Col md={6} lg={6} sm={8} xs={8} smOffset={1} xsOffset={1} >
                {/*<h3> About Us. </h3>
                <p> Somos agentes de cambio en la educación. </p>*/}
                <p className='justify pt-50'> Change es una empresa que busca mejorar la calidad de la enseñanza en el Perú. Creemos firmemente
                    que nuestro sistema educativo tiene muchos aspectos por mejorar y queremos enfocarnos en algunos de ellos. El mundo de hoy requiere
                    que las personas tengan ciertas habilidades y muchas veces, éstas, no son desarrolladas en los centros educativos. Sabemos también
                    de la importancia del inglés y rol fundamental que desempeña en el desarrollo profesional de las personas. Nuestro enfoque se
                    centra en ayudar a las personas a aprender.</p>
                <p className='justify pt-25 pb-50' > Uno de nuestros objetivos es realizar capacitación en el uso de nuevas metodologías y tecnologías
                    educativas. Además, asesoramos a centros educativos para que puedan integrar dichas metodologías
                    y tecnologías dentro de sus programas educativos. Finalmente, al considerar el idioma inglés como un componente
                    importante de la educación actual, diseñamos cursos a medida, que permitan a nuestros clientes cumplir sus objetivos personales y
                    profesionales.
                </p>
            </Col>
        </Row>
    )
}
