const Services = () => {
    return (
        <Row style={{ paddingLeft: '0', paddingRight: '0' }}>
            <Col style={{ paddingLeft: '0', paddingRight: '0', marginBottom: '50px' }}>
                <Image src={Service01} lg={12} md={12} sm={12} xs={12} style={{ width: '100%' }} />
            </Col>
            <Col style={{ paddingLeft: '0', paddingRight: '0', marginBottom: '50px' }}>
                <Image src={Service02} lg={12} md={12} sm={12} xs={12} style={{ width: '100%' }} />
            </Col>
            <Col style={{ paddingLeft: '0', paddingRight: '0', marginBottom: '50px' }}>
                <Image src={Service03} lg={12} md={12} sm={12} xs={12} style={{ width: '100%' }} />
            </Col>
        </Row>
    )
}

const View = () => {
    return (
        <Col md={11} lg={11} sm={10} xs={12} id='home'>
            <LogoHome />
            <Picture />
            <Title title={'Education, transformation and technology'} />
            <SecondSection />
            {/*<Mission />*/}
            <Services />
        </Col>
    )
}

export default View;


