import React from "react";
import logoHome from "../../img/icons/icons/home/logo para home.png";
import { Row, Col, Image } from "react-bootstrap";

const LogoHome = () => {
    return (
        <Row>
            <Col lg={6} md={6} xs={6} sm={6} >
                <section className='p-50'>
                    {/*<h1> Change </h1>
                    <p> cambiando la educación </p>*/}
                    <Image src={logoHome} style={{ width: '200px' }} />
                </section>
            </Col>
        </Row>
    )
}

export default LogoHome;


